import { PlatformShowErrorProps } from '@wix/error-handler-core/types';
import { ErrorToastContent } from '../components/ErrorToastContent';
import { render, RenderResult } from '@testing-library/react';
import React from 'react';
import { ErrorHandler } from '../error-handler/ErrorHandler';

export const showErrorWds = ({
  requestId,
  message,
  errorHandler,
}: PlatformShowErrorProps & {
  errorHandler: ErrorHandler;
}): RenderResult => {
  return render(
    <ErrorToastContent
      dataHook="error-toast-content"
      errorHandler={errorHandler}
      requestId={requestId}
      message={message}
    />,
  );
};
